/* Сброс */
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a,
a:hover {
  text-decoration: none;
  color: #fff;
}
body {
  font-family: "Montserrat", sans-serif;
  color: #2e2f3f;
  &.lock {
    @media (max-width: 900px) {
      overflow: hidden;
    }
  }
}

/*ОБЩИЕ СТИЛИ*/

.block-title {
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  color: #000000;
}
.block-subtitle {
  text-align: center;
  color: #000000;
  font-size: 18px;
}

.block-bold-text {
  color: #1d1d1d;
  font-size: 14px;
  font-weight: 700;
}

.block-light-text {
  color: #1d1d1d;
  font-size: 14px;
  line-height: 18px;
}


.block-btn {
  padding: 13px 33px;
  border-radius: 5px;
  border: 2px solid #e5dfdf;
  display: inline-block;
  cursor: pointer;
  & a {
    color: #1d1d1d;
    font-size: 14px;
    font-weight: 700;
    
  }
  &:hover{
    border-color: #01be01;
    & a{
      color: #01be01;
    }
  }
}

/* контейнер */

.container {
  max-width: 1140px;
  margin: 0 auto;
}

@media (max-width: 576px) {
  .container {
    max-width: 540px;
    padding: 0 20px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 720px;
    padding: 0 20px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 960px;
    padding: 0 10px;
  }
}
@media (max-width: 450px) {
  .container {
    padding: 0 20px;
  }
}
.header {
  position: relative;
  box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.1);
  #particles {
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
}

/* header */
.info-block {
  padding-top: 40px;
  padding-bottom: 50px;
  @media (max-width: 768px) {
    display: none;
  }
  .info-block-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .info-block-logo {
      display: flex;
      align-items: flex-start;
      .info-block-logo-image {
        width: 60px;
        height: 60px;
        margin-right: 18px;
        & img {
          width: 100%;
        }
      }
      .info-block-logo-info {
        &-text {
          font-size: 12px;
          & span {
            font-weight: 700;
            font-size: 24px;
          }
        }
      }
    }
    .info-block-phone {
      display: flex;
      align-items: center;
      font-size: 16px;
      font-weight: 700;
      & a{
        color: #000;
        &:hover{
          color: #01be01;
        }
      }
      
      & img {
        margin-right: 15px;
      }
    }
    .info-block-location {
      display: flex;
      align-items: center;
      max-width: 150px;
      font-size: 12px;
      & img {
        margin-right: 20px;
      }
    }
  }
}
/* nav */
.nav {
  padding-bottom: 10px;
  @media (max-width: 768px) {
    padding: 15px 0;
  }
  .nav-wrapper {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #f2f2f2;
    padding-top: 10px;
    @media (max-width: 768px) {
      align-items: center;
    }
    .menu-list {
      display: flex;
      align-items: center;
      .list-item {
        &:hover a{ 
          color: #01be01;
        }
        &:not(:last-child) {
          margin-right: 35px;
        }
        & a {
          color: #171717;
          font-size: 14px;
          font-weight: 700;
        }
      }
      @media (max-width: 768px) {
        display: none;
        &.collapse {
          display: flex;
        }
      }
    }
    .nav-search {
      width: 235px;
      height: 35px;
      border-radius: 18px;
      border: 1px solid #f3f3f3;
      padding: 14px 25px;
      outline: none;
      background: url(../img/header/search.png) no-repeat 92% 50%,#f7f7f7;
      @media (max-width: 768px) {
        display: none;
      }
      
    }
    &.turnmobile{
      border-top: none;
      padding-bottom: 10px;
      .info-block-phone{
        display: none;
      }
    }
  }
  .menu__burger {
    position: relative;
    width: 25px;
    height: 20px;
    display: none;
    z-index: 119;
    & span::before,
    & span::after {
      position: absolute;
      border: 1px solid #01be01;
    }
    & span::before {
      width: 15px;
    }
    & span::after {
      width: 20px;
    }
    & span::before {
      transition: all 0.4s ease-in;
      content: "";
      top: 0;
    }
    & span::after {
      transition: all 0.4s ease-in;
      content: "";
      bottom: 0;
    }
    &::after {
      position: absolute;
      width: 100%;
      border: 1px solid #01be01;
      content: "";
      top: 9px;
    }
    @media (max-width: 768px) {
      display: block;
    }
    &.collapsed span::before {
      transform: rotate(45deg);
      top: 9px;
    }
    &.collapsed span::after {
      transform: rotate(-45deg);
      bottom: 9px;
    }
    &.collapsed span::before {
      width: 100%;
      border: 1px solid #000;
    }
    &.collapsed span::after {
      width: 100%;
      border: 1px solid #000;
    }
    &.collapsed::after {
      border: none;
    }
    &.collapsed{
      position: absolute;
      top: 30px;
      right: 30px;
    }
  }
  .mobile-nav {
    display: none;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background: #fff;
    z-index: 99;
    margin-top: 80px;
    transition: all 0.5s linear;
    opacity: 0;
    @media (max-width: 768px) {
      display: block;
    }
    &.active {
      opacity: 1;
      position: fixed;
      overflow: auto;

    }
    .mobile-nav-list {
      background: #fff;
      padding: 0 10px;
      .mobile-list-item {
        font-size: 24px;
        font-weight: 700;
        letter-spacing: 0.8px;
        & a {
          color: #212123;
        }
        border-bottom: 1px solid rgb(192, 190, 190);
        padding: 15px 0;
        &.none-border {
          border: none;
        }
        @media (max-width: 768px) {
          font-size: 14px;
        }
        @media (max-width: 430px) {
          font-size: 13px;
          & img{
            display: inline-block;
            margin-right: 5px;
          }
        }
      }
    }
    .menu-item-has-children {
      position: relative;
      & li:last-child {
        border: none;
      }
      & li {
        font-size: 16px;
        opacity: 0.5;
        padding-left: 20px;
      }
    }
    .sub-menu {
      display: none;
    }
    .sidebar-menu-arrow {
      position: relative;
      padding-right: 15px;
    }
    .sidebar-menu-arrow::after {
      content: "\2227";
      transform: rotate(90deg);
      padding: 10px;
      color: #000;
      position: absolute;
      z-index: 999;
      right: 0;
      top: -10px;
    }
    .sidebar-menu-arrow.active::after, .sidebar-menu-arrow.active{
      color: #01be01;
    }
  }
  .info-block-logo {
    display: flex;
    align-items: flex-start;
    position: relative;
    z-index: 99;
    .info-block-logo-image {
      width: 40px;
      height: 40px;
      margin-right: 18px;
      & img {
        width: 100%;
      }
      @media (max-width: 450px) {
        width: 30px;
        height: 30px;
        margin-right: 5px;
      }
    }
    .info-block-logo-info {
      &-text {
        font-size: 12px;
        @media (max-width: 450px) {
          font-size: 10px;
        }
        & span {
          font-weight: 700;
          font-size: 24px;
          @media (max-width: 450px) {
            font-size: 18px;
          }
        }
      }
    }

    display: none;
    @media (max-width: 768px) {
      display: flex;
      align-items: center;
    }
  }
  .info-block-phone {
    display: none;
    @media (max-width: 768px) {
      display: block;
    }
  }
}

/* presentation */
.presentation {
  .slider-slide {
    outline: none;
    color: #ffffff;
    padding-top: 50px;
    padding-bottom: 132px;
    .slide-info {
      background: rgba(255, 255, 255, 0.2);
      padding: 80px 100px 43px 100px;
      max-width: 750px;
      text-align: center;
      margin: 0 auto;
      @media (max-width: 768px) {
        width: 100%;
        padding: 40px 50px 40px 50px;
      }
      @media (max-width: 578px) {
        padding: 20px 30px 20px 30px;
      }
      .slide-info-title {
        max-width: 559px;
        font-size: 36px;
        font-weight: 700;
        line-height: 36px;
        margin-bottom: 28px;
        @media (max-width: 768px) {
          font-size: 30px;
        }
      }
      .slide-info-text {
        max-width: 397px;
        font-size: 18px;
        line-height: 30px;
        margin: 0 auto;
        margin-bottom: 28px;
        @media (max-width: 768px) {
          max-width: 100%;
          font-size: 14px;
        }
      }
      .block-btn {
        background-color: #fe6547;
        padding: 12px 66px;
        border: none;
        @media (max-width: 768px) {
          padding: 12px 35px;
        }
        & a {
          display: inline-block;
          max-width: 115px;
          color: #fff;
          font-size: 16px;
          @media (max-width: 768px) {
            font-size: 14px;
          }
        }
      }
    }
  }
  .slick-next {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    right: 47.8%;
    transform: translateX(-50%);
    z-index: 1;
    background: #fff;
    padding: 10px;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(216, 215, 215);
    @media (max-width: 1500px) {
      right: 47.2%;
    }
    @media (max-width: 1320px) {
      right: 46.8%;
    }
    @media (max-width: 1240px) {
      right: 46.2%;
    }
    @media (max-width: 1080px) {
      right: 45.8%;
    }
    @media (max-width: 992px) {
      right: 45.6%;
    }
    @media (max-width: 922px) {
      right: 45.3%;
    }
    @media (max-width: 860px) {
      right: 45%;
    }
    @media (max-width: 800px) {
      right: 44.6%;
    }
  }
  .slick-prev {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 47.8%;
    transform: translateX(-50%);
    z-index: 1;
    background: #fff;
    padding: 10px;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(216, 215, 215);
    @media (max-width: 1500px) {
      left: 47.2%;
    }
    @media (max-width: 1320px) {
      left: 46.8%;
    }
    @media (max-width: 1240px) {
      left: 46.2%;
    }
    @media (max-width: 1080px) {
      left: 45.8%;
    }
    @media (max-width: 992px) {
      left: 45.6%;
    }
    @media (max-width: 922px) {
      left: 45.3%;
    }
    @media (max-width: 860px) {
      left: 45%;
    }
    @media (max-width: 800px) {
      left: 44.6%;
    }
  }
}
/* order */

.order {
  background-color: #f3f2f2;
  padding-top: 80px;
  padding-bottom: 30px;
  text-align: center;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .block-subtitle {
    margin-bottom: 65px;
    @media (max-width: 768px) {
      margin-bottom: 30px;
    }
  }
  .order-box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: left;
    .order-item {
      box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.05);
      border-radius: 5px;
      padding: 21px 21px 31px 21px;
      width: 48%;
      margin-bottom: 33px;
      background: #fff;
      @media (max-width: 1160px) {
        width: 49%;
      }
      @media (max-width: 768px) {
        width: 100%;
      }
      .order-image {
        width: 100%;
        height: 224px;
        position: relative;
        border-radius: 5px;
        border: 1px solid #dfdfdf;
        & img {
          display: block;
          padding: 8px;
          width: 100%;
          height: 100%;
        }
        .order-title {
          position: absolute;
          bottom: 8px;
          left: 0;
          padding: 15px 50px 15px 35px;
          background: rgba(255, 255, 255, 0.7);
          color: #000000;
          font-size: 18px;
          font-weight: 700;
          @media (max-width: 1160px) {
            padding: 15px 30px 15px 20px;
            font-size: 16px;
          }
          @media (max-width: 920px) {
            padding: 10px 20px 10px 20px;
            font-size: 14px;
          }
          @media (max-width: 768px) {
            font-size: 18px;
            padding: 15px 50px 15px 35px;
          }
          @media (max-width: 578px) {
            font-size: 16px;
            padding: 10px 20px 10px 20px;
          }
          @media (max-width: 460px) {
            font-size: 14px;
            padding: 10px 20px 10px 20px;
            width: 100%;
          }
        }
      }
      .order-price {
        max-width: 160px;
        color: #8a8989;
        font-size: 14px;
        margin: 25px 0;
        & span {
          color: #60a408;
          font-size: 24px;
          font-weight: 700;
        }
      }
    }
  }
}
/* preference */
.preference {
  padding-top: 60px;
  padding-bottom: 40px;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .preference-wrapper {
    text-align: center;
    .preference-box {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 35px 0;
      @media (max-width: 992px) {
        flex-wrap: wrap;
        justify-content: center;
      }
      .preference-box-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.11);
        border-radius: 5px;
        padding: 24px 50px 33px 50px;
        max-width: 24%;
        @media (max-width: 992px) {
          max-width: 33%;
          margin: 20px;
        }
        @media (max-width: 768px) {
          max-width: 33.3%;
          padding: 18px 30px 25px 30px;
          margin: 10px;
        }
        @media (max-width: 680px) {
          max-width: 48%;
        }
        @media (max-width: 578px) {
          padding: 14px 20px 20px 20px;
          margin: 5px;
        }
        @media (max-width: 480px) {
          max-width: 100%;
        }
        .block-bold-text,
        .block-light-text {
          max-width: 170px;
        }
        .preference-image {
          padding: 21px;
          border: 4px solid #494949;
          background-color: #371f1d;
          border-radius: 50%;
          margin-bottom: 20px;
          @media (max-width: 578px) {
            padding: 14px;
          }
          & img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
/* steps */
.steps {
  padding: 95px 0;
  background-color: #f3f2f2;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .steps-wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    @media (max-width: 520px) {
      flex-direction: column;
    }
    .steps-box {
      width: 70%;
      @media (max-width: 820px) {
        width: 60%;
      }
      @media (max-width: 520px) {
        width: 100%;
      }
      .block-title {
        margin-bottom: 60px;
        text-align: left;
        @media (max-width: 800px) {
          margin-bottom: 30px;
          font-size: 24px;
        }
        @media (max-width: 650px) {
          font-size: 20px;
        }
        @media (max-width: 520px) {
          font-size: 30px;
          text-align: center;
        }
      }
      .steps-slider {
        .steps-item {
          color: #f8f8f8;
          width: 360px;
          box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.11);
          border-radius: 5px;
          background-color: #ffffff;
          padding: 26px 18px 26px 26px;
          margin-right: 30px;
          @media (max-width: 1140px) {
            padding: 15px 10px 15px 15px;
            margin-right: 20px;
          }
          @media (max-width: 520px) {
            margin-right: 0;
          }
          .steps-number {
            padding: 22px 19px;
            border-radius: 50%;
            border: 4px solid #494949;
            background-color: #371f1d;
            width: 67px;
            height: 67px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 18px;
            & span {
              font-size: 30px;
              font-weight: 700;
            }
          }
          .block-bold-text {
            max-width: 276px;
            &::after {
              content: "";
              display: block;
              width: 50px;
              height: 1px;
              background-color: #b4b4b4;
              margin: 20px 0;
            }
          }
        }
      }
    }
    .request-call {
      color: #f8f8f8;
      border-radius: 5px;
      background-color: #f9895d;
      padding: 30px 25px;
      width: 360px;
      @media (max-width: 1140px) {
        padding: 18px 9px;
      }
      @media (max-width: 520px) {
        width: 100%;
        margin-top: 20px;
      }
      .request-image {
        width: 100%;
        padding: 8px;
        height: 203px;
        border-radius: 5px;
        border: 1px solid #f3f3f3;
        margin-bottom: 34px;
        @media (max-width: 992px) {
          margin-bottom: 15px;
        }
        & img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
      .request-title {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 20px;
        @media (max-width: 992px) {
          margin-bottom: 10px;
          font-size: 16px;
        }
      }
      .block-light-text {
        color: #f8f8f8;
        margin-bottom: 30px;
        @media (max-width: 992px) {
          font-size: 12px;
        }
        @media (max-width: 992px) {
          margin-bottom: 15px;
        }
      }
      .request-btn {
        font-size: 12px;
        padding: 13px;
        border: none;
        background-color: #2ed32e;
        & a {
          color: #f8f8f8;
        }
      }
    }
  }
  .slick-next {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 3.5%;
    z-index: 11;
    background: #fff;
    padding: 10px;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(216, 215, 215);
    @media (max-width: 1100px) {
      left: 4%;
    }
    @media (max-width: 992px) {
      left: 4.5%;
    }
    @media (max-width: 960px) {
      bottom: -20px;
      left: 4.9%;
    }
    @media (max-width: 780px) {
      left: 7%;
      bottom: 0;
    }
    @media (max-width: 600px) {
      left: 8.5%;
    }
    @media (max-width: 530px) {
      display: none !important;
    }
  }
  .slick-slider {
    position: static;
  }
  .slick-prev {
    cursor: pointer;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 11;
    background: #fff;
    padding: 10px;
    width: 40px;
    height: 40px;
    border: 1px solid rgb(216, 215, 215);
    @media (max-width: 960px) {
      bottom: -20px;
    }
    @media (max-width: 780px) {
      bottom: 0;
    }
    @media (max-width: 530px) {
      display: none !important;
    }
  }
}

/* portfolio */

.portfolio {
  padding: 85px 0;
  text-align: center;
  overflow: hidden;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .block-title {
    margin-bottom: 50px;
    @media (max-width: 768px) {
      margin-bottom: 30px;
    }
  }
  .portfolio-slide-wrapper {
    padding: 10px 5px;
    outline: none;
    width: 550px;
    @media (max-width: 768px) {
      width: 450px;
    }
    @media (max-width: 578px) {
      width: 370px;
    }
    @media (max-width: 440px) {
      width: 300px;
    }
  }
  .portfolio-slide {
    box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.09);
    border-radius: 5px;

    @media (max-width: 578px) {
      width: 100%;
    }
    .portfolio-image {
      width: 100%;
      height: 383px;
      @media (max-width: 578px) {
        height: 300px;
      }
      @media (max-width: 440px) {
        height: 240px;
      }
      & img {
        width: 100%;
        height: 100%;
      }
    }
    .portfolio-info {
      padding: 25px 80px;
      @media (max-width: 578px) {
        padding: 15px 30px;
      }
    }
  }
  .block-btn {
    margin-top: 55px;
    @media (max-width: 768px) {
      margin-top: 40px;
    }
  }
  .slick-list {
    overflow: visible;
  }
  .slick-slide {
    margin: 0 25px;
    @media (max-width: 1140px) {
      margin: 0 15px;
    }
    @media (max-width: 578px) {
      margin: 0;
    }
  }
  .slick-list {
    margin-right: -25px;
    margin-left: -25px;
    position: relative;
    @media (max-width: 1140px) {
      margin: 0 -15px;
    }
    @media (max-width: 578px) {
      margin: 0;
    }
  }
}
/* about */
.about {
  background: #f3f2f2;
  padding: 85px 0;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .about-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 35px 50px 50px;
    background: #fff;
    box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.06);
    border-radius: 5px;
    @media (max-width: 1220px) {
      width: 95%;
      margin: 0 auto;
    }
    @media (max-width: 992px) {
      padding: 25px 15px 25px 25px;
    }
    @media (max-width: 768px) {
      padding: 15px;
      flex-direction: column;
      align-items: flex-start;
    }
    .about-image {
      border-radius: 5px;
      border: 1px solid #d8d0d0;
      margin-right: 25px;
      width: 388px;
      max-width: 100%;
      height: 388px;
      @media (max-width: 768px) {
        margin: 0 auto;
      }
      @media (max-width: 480px) {
        height: 300px;
      }
      @media (max-width: 420px) {
        height: 250px;
      }
      & img {
        display: block;
        padding: 10px;
        width: 100%;
        height: 100%;
      }
    }
    .about-info {
      color: #1d1d1d;
      max-width: 650px;
      @media (max-width: 768px) {
        max-width: 100%;
      }
      .about-info-title {
        font-size: 30px;
        font-weight: 700;
        @media (max-width: 768px) {
          text-align: center;
          margin-top: 10px;
        }
        &::after {
          content: "";
          display: block;
          width: 116px;
          height: 1px;
          background-color: #b4b4b4;
          margin-top: 35px;
          margin-bottom: 30px;
          @media (max-width: 992px) {
            margin-top: 15px;
            margin-bottom: 10px;
          }
          @media (max-width: 768px) {
            margin: 0 auto;
            margin-top: 15px;
            margin-bottom: 10px;
          }
        }
      }
      .block-light-text {
        margin-top: 30px;
      }
    }
  }
}

/* feedback */
.feedback {
  text-align: center;
  padding: 85px 0;
  position: relative;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .slide-wrapper {
    padding: 15px;
    outline: none;
  }
  .feedback-slide {
    color: #1d1d1d;
    display: flex;
    flex-direction: column;
    box-shadow: 2px 3px 7px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 35px 35px 80px 35px;
    text-align: left;
    @media (max-width: 1240px) {
      padding: 35px;
    }
    @media (max-width: 880px) {
      padding: 25px;
    }
    .feedback-author {
      display: flex;
      align-items: center;
      margin-bottom: 30px;

      .feedback-author-photo {
        width: 93px;
        height: 93px;
        margin-right: 38px;
        border-radius: 50%;
        @media (max-width: 578px) {
          width: 60px;
          height: 60px;
        }
        & img {
          width: 100%;
        }
      }
      .feedback-author-name {
        font-size: 16px;
        font-weight: 700;
      }
    }
    .feedback-text {
      font-size: 16px;
    }
  }
  .block-btn {
    margin-top: 54px;
    @media (max-width: 768px) {
      margin-top: 15px;
    }
  }
  .block-title {
    margin-bottom: 50px;
    @media (max-width: 992px) {
      margin-bottom: 30px;
    }
    @media (max-width: 768px) {
      margin-bottom: 15px;
    }
  }
  .slick-slide {
    margin: 0 5px;
    @media (max-width: 1050px) {
      margin: 0 5px;
    }
    @media (max-width: 880px) {
      margin: 0;
    }
  }
  .slick-list {
    margin-right: -5px;
    margin-left: -5px;
    position: relative;
    @media (max-width: 1050px) {
      margin: 0 -5px;
    }
    @media (max-width: 880px) {
      margin: 0;
    }
  }
}

/* map */
.map {
  height: 440px;
}

/* footer */
.footer {
  background-color: #383839;
  padding: 60px 0;
  color: #f3eeee;
  @media (max-width: 578px) {
    padding: 45px 0;
  }
  .footer-wrapper {
    display: flex;
    justify-content: space-between;
    @media (max-width: 880px) {
      flex-wrap: wrap;
    }
    .footer-item {
      display: flex;
      flex-direction: column;
      @media (max-width: 880px) {
        width: 45%;
        margin: 10px;
      }
      @media (max-width: 520px) {
        width: 100%;
        margin: 0;
        margin-bottom: 10px;
      }
      .footer-logo {
        display: flex;
        align-items: flex-start;
        margin-bottom: 30px;
        .footer-logo-image {
          width: 60px;
          height: 60px;
          margin-right: 18px;
          & img {
            width: 100%;
          }
        }
        .footer-logo-info {
          &-text {
            font-size: 12px;
            & span {
              font-weight: 700;
              font-size: 24px;
            }
          }
        }
      }
      .footer-phone {
        display: flex;
        align-items: center;
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 18px;
        &:hover a{
          color: #01be01;
        }
        & img {
          margin-right: 15px;
        }
      }
      .footer-location {
        display: flex;
        align-items: center;
        max-width: 150px;
        font-size: 12px;
        & img {
          margin-right: 20px;
        }
      }
    }
    .footer-title {
      font-size: 14px;
      font-weight: 700;
      margin-bottom: 13px;
    }
    .footer-list-icon {
      display: flex;
      align-items: center;
      .list-item {
        &:not(:last-child) {
          margin-right: 20px;
        }
      }
    }
    .footer-list {
      .list-item a {
        color: #f3eeee;
        font-size: 14px;
        &:hover{
          color: #01be01;
        }
      }
    }
  }
}
.slick-next,.slick-prev{
  &:hover{
    background: #01be01;
    border-color: #01be01;
  }
}
// СТРАНИЦА РЕМОНТА, ТИПОВАЯ ТЕКСТОВАЯ
.repair{
  padding: 40px 0;
  .block-title{
      margin-bottom: 50px;
      font-weight: 700;
      @media (max-width: 768px){
          margin-top: 50px;
      }
      &::before{
          display: none;
      }
  }
  .repair-article{
      margin-bottom: 30px;
      .float-left{
          float: left;
          padding-right: 30px;
          padding-bottom: 30px;
          @media (max-width: 992px){
              width: 450px;
          }
          @media (max-width: 768px){
              width: 350px;
          }
          @media (max-width: 620px){
              float: none;
              width: 100%;
              padding: 0;
              margin-bottom: 20px;
              text-align: center;
          }
      }
      .float-right{
          float: right;
          padding-left: 27px;
          padding-bottom: 30px;
          @media (max-width: 992px){
              width: 450px;
          }
          @media (max-width: 768px){
              width: 350px;
          }
          @media (max-width: 620px){
              float: none;
              width: 100%;
              padding: 0;
              margin-bottom: 20px;
              text-align: center;
          }
      }
      .repair-text{
          color: #212123;
          font-size: 14px;
          letter-spacing: -0.4px;
          @media (max-width: 620px){
              text-align: justify;
          }
      }
      .repair-service-list{
          margin-left: 20px;
          list-style: square;
          margin-bottom: 15px;
          .list-item{
              color: #212123;
              font-size: 14px;
              letter-spacing: -0.4px;
          }
      }
  }
}
// СТРАНИЦА CONTACT
.contact{
  margin-top: 50px;
  .block-title{
      margin-bottom: 60px;
      font-weight: 700;
      &::before{
          display: none;
      }
  }
  .contact-box{
      display: flex;
      justify-content: space-between;
      box-shadow: 1px 2px 14px 2px rgba(0, 0, 0, 0.2);
      margin-bottom: 120px;
      .block-title{
          text-align: left;
      }
      .contact-box-info{
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          padding: 60px;
          @media (max-width: 768px){
              padding: 35px;
          }
          @media (max-width: 450px){
              padding: 20px;
          }
          .contact-list{
              .list-item{
                  margin-bottom: 33px;
                  &:last-child{
                      margin-bottom: 45px;
                  }
              }
          }
          .download-company-cart{
              display: flex;
              align-items: center;
              color: #212123;
              max-width: 290px;
              margin-bottom: 44px;
              & img{
                  margin-right: 20px;
              }
          }
          .social-text{
              font-weight: 700;
              margin-bottom: 30px;
          }
          .social-icon-box{
              display: flex;
              .social-btn{
                  &:not(:last-child){
                      margin-right: 10px;
                  }
              }
              .social-btn img{
                  width: 50px;
                  height: 50px;
              }
          }
      }
      .contact-box-map{
          width: 50%;
          @media (max-width: 768px){
              width: 100%;
              height: 300px;
          }
      }
      @media (max-width: 768px){
          flex-direction: column;
      }
  }
}