@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

/* Сброс */
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a,
a:hover {
  text-decoration: none;
  color: #fff;
}
body {
  font-family: "Montserrat", sans-serif;
  color: #2e2f3f;
}
@media (max-width: 900px) {
  body.lock {
    overflow: hidden;
  }
}
/*ОБЩИЕ СТИЛИ*/
.block-title {
  text-align: center;
  font-size: 30px;
  font-weight: 700;
  color: #000;
}
.block-subtitle {
  text-align: center;
  color: #000;
  font-size: 18px;
}
.block-bold-text {
  color: #1d1d1d;
  font-size: 14px;
  font-weight: 700;
}
.block-light-text {
  color: #1d1d1d;
  font-size: 14px;
  line-height: 18px;
}
.block-btn {
  padding: 13px 33px;
  border-radius: 5px;
  border: 2px solid #e5dfdf;
  display: inline-block;
  cursor: pointer;
}
.block-btn a {
  color: #1d1d1d;
  font-size: 14px;
  font-weight: 700;
}
.block-btn:hover {
  border-color: #01be01;
}
.block-btn:hover a {
  color: #01be01;
}
/* контейнер */
.container {
  max-width: 1140px;
  margin: 0 auto;
}
@media (max-width: 576px) {
  .container {
    max-width: 540px;
    padding: 0 20px;
  }
}
@media (max-width: 768px) {
