}
.header {
  position: relative;
  box-shadow: 2px 3px 7px rgba(0,0,0,0.1);
}
.header #particles {
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}
/* header */
