/* nav */
.nav {
  padding-bottom: 10px;
}
@media (max-width: 768px) {
  .nav {
    padding: 15px 0;
  }
}
.nav .nav-wrapper {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #f2f2f2;
  padding-top: 10px;
}
@media (max-width: 768px) {
  .nav .nav-wrapper {
    align-items: center;
  }
}
.nav .nav-wrapper .menu-list {
  display: flex;
  align-items: center;
}
.nav .nav-wrapper .menu-list .list-item:hover a {
  color: #01be01;
}
.nav .nav-wrapper .menu-list .list-item:not(:last-child) {
  margin-right: 35px;
}
.nav .nav-wrapper .menu-list .list-item a {
  color: #171717;
  font-size: 14px;
  font-weight: 700;
}
@media (max-width: 768px) {
  .nav .nav-wrapper .menu-list {
    display: none;
  }
  .nav .nav-wrapper .menu-list.collapse {
    display: flex;
  }
}
.nav .nav-wrapper .nav-search {
  width: 235px;
  height: 35px;
  border-radius: 18px;
  border: 1px solid #f3f3f3;
  padding: 14px 25px;
  outline: none;
  background: url("../img/header/search.png") no-repeat 92% 50%, #f7f7f7;
}
@media (max-width: 768px) {
  .nav .nav-wrapper .nav-search {
    display: none;
  }
}
.nav .nav-wrapper.turnmobile {
  border-top: none;
  padding-bottom: 10px;
}
.nav .nav-wrapper.turnmobile .info-block-phone {
  display: none;
}
.nav .menu__burger {
  position: relative;
  width: 25px;
  height: 20px;
  display: none;
  z-index: 119;
}
.nav .menu__burger span::before,
.nav .menu__burger span::after {
  position: absolute;
  border: 1px solid #01be01;
}
.nav .menu__burger span::before {
  width: 15px;
}
.nav .menu__burger span::after {
  width: 20px;
}
.nav .menu__burger span::before {
  transition: all 0.4s ease-in;
  content: "";
  top: 0;
}
.nav .menu__burger span::after {
  transition: all 0.4s ease-in;
  content: "";
  bottom: 0;
}
.nav .menu__burger::after {
  position: absolute;
  width: 100%;
  border: 1px solid #01be01;
  content: "";
  top: 9px;
}
@media (max-width: 768px) {
  .nav .menu__burger {
    display: block;
  }
}
.nav .menu__burger.collapsed span::before {
  transform: rotate(45deg);
  top: 9px;
}
.nav .menu__burger.collapsed span::after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.nav .menu__burger.collapsed span::before {
  width: 100%;
  border: 1px solid #000;
}
.nav .menu__burger.collapsed span::after {
  width: 100%;
  border: 1px solid #000;
}
.nav .menu__burger.collapsed::after {
  border: none;
}
.nav .menu__burger.collapsed {
  position: absolute;
  top: 30px;
  right: 30px;
}
.nav .mobile-nav {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: #fff;
  z-index: 99;
  margin-top: 80px;
  transition: all 0.5s linear;
  opacity: 0;
}
@media (max-width: 768px) {
  .nav .mobile-nav {
    display: block;
  }
}
.nav .mobile-nav.active {
  opacity: 1;
  position: fixed;
  overflow: auto;
}
.nav .mobile-nav .mobile-nav-list {
  background: #fff;
  padding: 0 10px;
}
.nav .mobile-nav .mobile-nav-list .mobile-list-item {
  font-size: 24px;
  font-weight: 700;
  letter-spacing: 0.8px;
  border-bottom: 1px solid #c0bebe;
  padding: 15px 0;
}
.nav .mobile-nav .mobile-nav-list .mobile-list-item a {
  color: #212123;
}
.nav .mobile-nav .mobile-nav-list .mobile-list-item.none-border {
  border: none;
}
@media (max-width: 768px) {
  .nav .mobile-nav .mobile-nav-list .mobile-list-item {
    font-size: 14px;
  }
}
@media (max-width: 430px) {
  .nav .mobile-nav .mobile-nav-list .mobile-list-item {
    font-size: 13px;
  }
  .nav .mobile-nav .mobile-nav-list .mobile-list-item img {
    display: inline-block;
    margin-right: 5px;
  }
}
.nav .mobile-nav .menu-item-has-children {
  position: relative;
}
.nav .mobile-nav .menu-item-has-children li:last-child {
  border: none;
}
.nav .mobile-nav .menu-item-has-children li {
  font-size: 16px;
  opacity: 0.5;
  padding-left: 20px;
}
.nav .mobile-nav .sub-menu {
  display: none;
}
.nav .mobile-nav .sidebar-menu-arrow {
  position: relative;
  padding-right: 15px;
}
.nav .mobile-nav .sidebar-menu-arrow::after {
  content: "\2227";
  transform: rotate(90deg);
  padding: 10px;
  color: #000;
  position: absolute;
  z-index: 999;
  right: 0;
  top: -10px;
}
.nav .mobile-nav .sidebar-menu-arrow.active::after,
.nav .mobile-nav .sidebar-menu-arrow.active {
  color: #01be01;
}
.nav .info-block-logo {
  display: flex;
